import type { IRootState } from '@/store'
import { ILyric, parseLyric } from '@/utils/parse-lyric'
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import {
  getSimiPlaylist,
  getSimiSong,
  getSongDetail,
  getSongLyric
} from '../service/player'
import { getPlaylistDetail } from '../../discover/c-views/recommend/service/recommend'

interface IThunkState {
  state: IRootState
}

export const fetchSongListAction = createAsyncThunk<void, number, IThunkState>(
  'liststSong',
  (id: number, { dispatch, getState }) => {
    // , 
    // 1.
    const playSongList = getState().player.playSongList
    getPlaylistDetail(id).then((res) => {
      // 1.song
      if (!res.playlist.tracks) return
      const song = res.playlist.tracks[0]
      const list = res.playlist.tracks.slice(0, 50)

      // 2.songcurrentSong
      const newPlaySongList = [...playSongList]
      list.concat(newPlaySongList)
      dispatch(changeCurrentSongAction(song))
      dispatch(changePlaySongListAction(list))
      dispatch(changePlaySongIndexAction(0))

      getSongLyric(song.id).then((res) => {
        // 1.
        const lyricString = res.lrc.lyric
        // 2.()
        const lyrics = parseLyric(lyricString)
        // 3.state
        dispatch(changeLyricsAction(lyrics))
      })
    })
    // 2.
  }
)

export const fetchCurrentSongAction = createAsyncThunk<
  void,
  number,
  IThunkState
>('currentSong', (id: number, { dispatch, getState }) => {
  // , 
  // 1.
  const playSongList = getState().player.playSongList
  const findIndex = playSongList.findIndex((item) => item.id === id)
  if (findIndex === -1) {
    // 
    // 1.
    getSongDetail(id).then((res) => {
      // 1.song
      if (!res.songs.length) return
      const song = res.songs[0]

      // 2.songcurrentSong
      const newPlaySongList = [...playSongList]
      newPlaySongList.push(song)
      dispatch(changeCurrentSongAction(song))
      dispatch(changePlaySongListAction(newPlaySongList))
      dispatch(changePlaySongIndexAction(newPlaySongList.length - 1))
    })
  } else {
    // item
    const song = playSongList[findIndex]
    dispatch(changeCurrentSongAction(song))
    dispatch(changePlaySongIndexAction(findIndex))
  }

  // 2.
  getSongLyric(id).then((res) => {
    // 1.
    const lyricString = res.lrc.lyric
    // 2.()
    const lyrics = parseLyric(lyricString)
    // 3.state
    dispatch(changeLyricsAction(lyrics))
  })
})

export const getSimiSongAction = createAsyncThunk<void, number, IThunkState>(
  'simiSong',
  (id, { dispatch }) => {
    getSimiPlaylist(id).then((res) => {
      dispatch(changeSimiPlaylistAction(res.playlists))
      console.log(res, 'getSimiPlaylist')
    })
    getSimiSong(id).then((res) => {
      dispatch(changeSimiSongsAction(res.songs))
    })
    console.log('simi')
  }
)
export const getDetailSongAction = createAsyncThunk<void, number, IThunkState>(
  'detailSong',
  (id, { dispatch }) => {
    getSongDetail(id).then((res) => {
      dispatch(changeCurrentSongDAction(res.songs[0]))
      console.log(res, 'res')
    })
    getSongLyric(id).then((res) => {
      const lyricString = res.lrc.lyric
      const lyrics = parseLyric(lyricString)
      dispatch(changeLyricsDAction(lyrics))
    })
  }
)

export const changeMusicAction = createAsyncThunk<void, boolean, IThunkState>(
  'changemuisc',
  (isNext, { dispatch, getState }) => {
    // 1.state
    const player = getState().player
    const playMode = player.playMode
    const songIndex = player.playSongIndex
    const songList = player.playSongList

    // 2.
    let newIndex = songIndex
    if (playMode === 1) {
      // 
      newIndex = Math.floor(Math.random() * songList.length)
    } else {
      // 
      newIndex = isNext ? songIndex + 1 : songIndex - 1
      if (newIndex > songList.length - 1) newIndex = 0
      if (newIndex < 0) newIndex = songList.length - 1
    }

    // 3.
    const song = songList[newIndex]
    dispatch(changeCurrentSongAction(song))
    dispatch(changePlaySongIndexAction(newIndex))

    // 4.
    getSongLyric(song.id).then((res) => {
      // 1.
      const lyricString = res.lrc.lyric
      // 2.()
      const lyrics = parseLyric(lyricString)
      // 3.state
      dispatch(changeLyricsAction(lyrics))
    })
  }
)

