import React, { memo, useEffect, useState } from 'react'
import type { FC, ReactNode } from 'react'
import { NavLink } from 'react-router-dom'
import { Avatar, Input } from 'antd'
import { SearchOutlined } from '@ant-design/icons'
import { HeaderLeft, HeaderRight, HeaderWrapper } from './style'
import { shallowEqualApp, useAppSelector } from '@/store'
import headerTitles from '@/assets/data/header_titles.json'
import { Button, Modal } from 'antd'
import Login from '../../views/login'
interface IProps {
  children?: ReactNode
}

const AppHeader: FC<IProps> = () => {
  /**  */
  // const [currentIndex, setCurrentIndex] = useState(0)

  /**  */
  const [isModalOpen, setIsModalOpen] = useState(false)
  function showItem(item: any) {
    if (item.type === 'path') {
      return (
        <NavLink
          to={item.link}
          className={({ isActive }) => {
            return isActive ? 'active' : undefined
          }}
        >
          {item.title}
          <i className="icon sprite_01"></i>
        </NavLink>
      )
    } else {
      return (
        <a href={item.link} rel="noreferrer" target="_blank">
          {item.title}
        </a>
      )
    }
  }

  const showModal = () => {
    setIsModalOpen(true)
  }

  const handleOk = () => {
    setIsModalOpen(false)
  }

  const handleCancel = () => {
    setIsModalOpen(false)
  }

  const { loginInfo } = useAppSelector(
    (state) => ({
      loginInfo: state.login.loginDetail
    }),
    shallowEqualApp
  )
  return (
    <HeaderWrapper>
      <div className="content wrap-v1">
        <HeaderLeft>
          <a className="logo sprite_01" href="/">
            
          </a>
          <div className="title-list">
            {headerTitles.map((item) => {
              return (
                <div className="item" key={item.title}>
                  {showItem(item)}
                </div>
              )
            })}
          </div>
        </HeaderLeft>
        <HeaderRight isLogin={loginInfo.account !== undefined}>
          <Input
            className="search"
            placeholder="///"
            prefix={<SearchOutlined />}
          />
          <span className="center"></span>
          {
            <span className="login" onClick={showModal}>
              
            </span>
          }
          <Avatar
            icon={<img src={loginInfo.profile?.avatarUrl} />}
            className="loginInfo"
          />
          <Modal
            title=""
            open={isModalOpen}
            onOk={handleOk}
            onCancel={handleCancel}
            footer={[<a key="other">&gt;</a>]}
          >
            <Login onOk={handleOk} />
          </Modal>
        </HeaderRight>
      </div>
      <div className="divider"></div>
    </HeaderWrapper>
  )
}

export default memo(AppHeader)
